import React from 'react';
import wrapDisplayName from 'recompose/wrapDisplayName';
import { auth } from '../db/firebase';

const withFirebaseUser = () => BaseComponent =>
  class WithFirebaseUser extends React.Component {
    static displayName = process.env.NODE_ENV !== 'production'
      ? wrapDisplayName(BaseComponent, 'withFirebaseUser')
      : undefined;

    state = {
      user: null
    };

    componentDidMount() {
      this.unregisterAuthObserver = auth.onAuthStateChanged(user =>
        this.setState({ user })
      );
    }

    componentWillUnmount() {
      this.unregisterAuthObserver();
    }

    render() {
      return React.createElement(BaseComponent, {
        ...this.props,
        user: this.state.user
      });
    }
  };

export default withFirebaseUser;
